* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  display: flex; 
  align-items: center;
  justify-content: center;
  background-color: aquamarine;
}

input{
  width: 360px;
  height: 50px;
  position: relative;
  border-radius: 30px;
  border-color: aqua;
 
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  
}

ul{
  position: absolute;
  display: flex;
  margin-top: 400px;
}
 li{
  list-style: none;
  margin: 20px;
  
 }

 p{
  max-width: 100px;
 }
